import { IDataRowSerializer } from './i-data-row-serializer';
import { DataRow } from '../../param-data-vo/data-row';
import { DimensionDataRow } from '../../param-data-vo/dimension-data-row';
import { DataItem } from '../../param-data-vo/data-item';

export class DimensionDataRowSerializer implements IDataRowSerializer{

    serialize(dataRow:DataRow):Object{
        let jobj=new Object();
        if(!dataRow){
            return null;
        }
        let dimensionDataRow=dataRow as DimensionDataRow;
        jobj['id']=dimensionDataRow.id;
        jobj['kind']=dimensionDataRow.kind;
        if(dimensionDataRow.dimensionDataItems){
            jobj['dimensionDataItems']=[];
            dimensionDataRow.dimensionDataItems.forEach(dimensionDataItem => {
                jobj['dimensionDataItems']=dimensionDataItem;
            });
        }
        if(dimensionDataRow.paramDataItems){
            jobj['paramDataItems']=[];
            dimensionDataRow.paramDataItems.forEach(paramItem => {
                jobj['paramDataItems']=paramItem;
            });
        }
        return jobj;
    }

    deserialize(jobj:Object):DataRow{
        if(!jobj){
            return null;
        }
        let dataRow=new DimensionDataRow();
        dataRow.id=jobj['id'];
        if(jobj['dimensionDataItems']){
            dataRow.dimensionDataItems=new Array<DataItem>();
            jobj['dimensionDataItems'].forEach(dimensionDataItemJobj => {
                dataRow.dimensionDataItems.push(Object.assign(new DataItem(),dimensionDataItemJobj));
            });
        }
        if(jobj['paramDataItems']){
            dataRow.paramDataItems=new Array<DataItem>();
            jobj['paramDataItems'].forEach(paramDataItemsJobj => {
                dataRow.dimensionDataItems.push(Object.assign(new DataItem(),paramDataItemsJobj));
            });
        }
        return dataRow;
    }
    
}