import { DefaultDataRow } from '../../param-data-vo/default-data-row';
import { DataRow } from '../../param-data-vo/data-row';
import { DataItem } from '../../param-data-vo/data-item';
import { IDataRowSerializer } from './i-data-row-serializer';

export class DefaultDataRowSerializer implements IDataRowSerializer{

    serialize(dataRow:DataRow):Object{
        let jobj=new Object();
        if(!dataRow){
            return null;
        }
        let defaultDataRow=dataRow as DefaultDataRow;
        jobj['id']=defaultDataRow.id;
        jobj['kind']=defaultDataRow.kind;
        if(defaultDataRow.paramItems){
            jobj['paramItems']=[];
            defaultDataRow.paramItems.forEach(paramItem => {
                jobj['paramItems']=paramItem;
            });
        }
        return jobj;
    }

    deserialize(jobj:Object):DataRow{
        if(!jobj){
            return null;
        }
        let dataRow=new DefaultDataRow();
        dataRow.id=jobj['id'];
        if(jobj['paramItems']){
            dataRow.paramItems=new Array<DataItem>();
            jobj['paramItems'].forEach(paramItemJobj => {
                dataRow.paramItems.push(Object.assign(new DataItem(),paramItemJobj));
            });
        }
        return dataRow;
    }
}