import { IDataTypeSerializer, ISerializeContext, IDataType, DefaultStructuredTypeSerializer } from '@ecp-caf/common-structure';
import { HelpType } from '../../extension/data-type/help-type';
import { SerializerUtils } from '../../utils/serializer-utils';

export class HelpTypeSerialzier implements IDataTypeSerializer{
    
    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        let jobj=new Object();
        if(!type){
            return null;
        }
        let serializer=new DefaultStructuredTypeSerializer();
        jobj=serializer.serialize(type,SerializerUtils.getContext());
        let helpType=type as HelpType;
        jobj['helpId']=helpType.helpId;
        jobj['helpCode']=helpType.helpCode;
        jobj['helpName']=helpType.helpName;
        jobj['kind']=helpType.kind;
        return jobj;
    }    
    
    deserialize(obj: Object, ctxt:ISerializeContext): IDataType {
        if(!obj){
            return null;
        }
        let helpType=new HelpType();
        let serializer=new DefaultStructuredTypeSerializer();
        helpType=serializer.deserialize(obj,SerializerUtils.getContext()) as HelpType;
        helpType.helpId=obj['helpId'];
        helpType.helpCode=obj['helpCode'];
        helpType.helpName=obj['helpName'];
        return helpType;
    }

}