import { IDataTypeSerializer, ISerializeContext, IDataType } from '@ecp-caf/common-structure';
import { StructureTypeRef } from '../../extension/data-type/structure-type-ref';
import { StructureTypeRefEnum } from '../../extension/data-type/structure-type-ref-enum';

export class StructureTypeRefSerializer implements IDataTypeSerializer{

    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        if(!type){
            return null;
        }
        let jobj=new Object();
        let refType=type as StructureTypeRef;
        jobj['kind']=refType.kind;
        jobj['refId']=refType.refId;
        jobj['type']=refType.type;
        return jobj;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext): IDataType {
        if(!obj){
            return null;
        }
        let refType=new StructureTypeRef();
        refType.refId=obj['refId'];
        refType.type=obj['type'] as StructureTypeRefEnum;
        return refType;
    }

}