import { UbccDecimalConstraint } from '../../extension/constraint/ubcc-decimal-constraint';
import { IConstraint, IConstraintSerializer, ISerializeContext } from '@ecp-caf/common-structure';

export class UbccDecimalConstraintSerializer implements IConstraintSerializer{
    
    serialize(constraint:IConstraint,ctxt: ISerializeContext):Object{
        let jobj=new Object();
        if(!constraint){
            return jobj;
        }
        let decimalConstraint=constraint as UbccDecimalConstraint;
        jobj['kind']=decimalConstraint.kind;
        jobj['max']=decimalConstraint.max;
        jobj['min']=decimalConstraint.min;
        jobj['precision']=decimalConstraint.precision;
        jobj['unit']=decimalConstraint.unit;
        return jobj;
    }

    deserialize(jobj:Object,ctxt: ISerializeContext):IConstraint{
        if(!jobj){
            return null;
        }
        let decimalConstraint=new UbccDecimalConstraint();
        decimalConstraint.max=jobj['max'] as number;
        decimalConstraint.min=jobj['min'] as number;
        decimalConstraint.precision=jobj['precision'] as number;
        decimalConstraint.unit=jobj['unit'];
        return decimalConstraint;
    }
}