import { IConstraint, IConstraintSerializer, ISerializeContext } from '@ecp-caf/common-structure';
import { UbccStringConstraint } from '../../extension/constraint/ubcc-string-constaint';

export class UbccStringConstraintSerializer implements IConstraintSerializer{

    serialize(constraint:IConstraint,ctxt: ISerializeContext):Object{
        let jobj=new Object();
        if(!constraint){
            return jobj;
        }
        let stringConstraint=constraint as UbccStringConstraint;
        jobj['kind']=stringConstraint.kind;
        jobj['maxLength']=stringConstraint.maxLength;
        jobj['minLength']=stringConstraint.minLength;
        jobj['regexExpress']=stringConstraint.regexExpress;
        return jobj;
    }

    deserialize(jobj:Object,ctxt: ISerializeContext):IConstraint{
        if(!jobj){
            return null;
        }
        let constaint=new UbccStringConstraint();
        constaint.maxLength=jobj['maxLength'] as number;
        constaint.minLength=jobj['minLength'] as number;
        constaint.regexExpress=jobj['regexExpress'];
        return constaint;
    }

}