import { UbccDateTimeConstraint } from '../../extension/constraint/ubcc-date-time-constraint';
import { IConstraint, IConstraintSerializer, ISerializeContext } from '@ecp-caf/common-structure';

export class UbccDateTimeConstraintSerializer implements IConstraintSerializer{

    serialize(constraint:IConstraint,ctxt: ISerializeContext):Object{
        let jobj=new Object();
        if(!constraint){
            return jobj;
        }
        let dataTimeConstraint=constraint as UbccDateTimeConstraint;
        jobj['kind']=dataTimeConstraint.kind;
        jobj['format']=dataTimeConstraint.format;
        return jobj;
    }

    deserialize(jobj:Object,ctxt: ISerializeContext):IConstraint{
        if(!jobj){
            return null;
        }
        let dataTimeConstraint=new UbccDateTimeConstraint();
        dataTimeConstraint.format=jobj['format'];
        return dataTimeConstraint;
    }

}