import { UbccParamSerializerContext } from '../serializer/serializer-context/ubcc-serializer-context';
import { UbccParamDataRowSerializerFactory } from '../serializer/factory/ubcc-param-data-row-serializer-factory';

// @dynamic
export class SerializerUtils{

    private static context:UbccParamSerializerContext;
    private static dataRowFactory:UbccParamDataRowSerializerFactory;
  
    public static getContext():UbccParamSerializerContext{
        if(!SerializerUtils.context){
            SerializerUtils.context=new UbccParamSerializerContext();
        }
        return SerializerUtils.context;
    }

    public static getUbccParamDataRowSerializerFactory():UbccParamDataRowSerializerFactory{
        if(!SerializerUtils.dataRowFactory){
            SerializerUtils.dataRowFactory=new UbccParamDataRowSerializerFactory();
        }
        return SerializerUtils.dataRowFactory;
    }
}