import { IDataRowSerializer } from '../param-data-serializer/i-data-row-serializer';
import { DataRowExtensionConst } from '../../../const/data-row-extension-const';
import { DefaultDataRowSerializer } from '../param-data-serializer/default-data-row-serializer';
import { DimensionDataRowSerializer } from '../param-data-serializer/dimension-data-row-serializer';

export class UbccParamDataRowSerializerFactory{
    
    get(kind: string): IDataRowSerializer {
        switch (kind) {
            case DataRowExtensionConst.DEFAULT_DATA_ROW:
                return new DefaultDataRowSerializer();
            case DataRowExtensionConst.DIMENSION_DATA_ROW:
                return new DimensionDataRowSerializer();
            default:
                throw new Error(`æªè·åå°${kind}ç±»åçDATA_ROWåºååå¨`);
        }
    }

}