import { DimensionInfo } from '../../param-template-assignment-vo/dimension-info';
import { DimensionSerializer } from '../dimension-serializer/dimension-serializer';
import { DimensionDataSerializer } from './dimension-data-serializer';

export class DimensionInfoSerializer{
    serialize(dimensionInfo:DimensionInfo):Object{
        if(!dimensionInfo){
            return null;
        }
        let jobj=new Object();
        jobj['dataModificationAllow']=dimensionInfo.dataModificationAllow;

        if(dimensionInfo.data){
            jobj['data']=dimensionInfo.data.map(dimensionData=>new DimensionDataSerializer().serialize(dimensionData));
        }

        if(dimensionInfo.definition){
            let serializer=new DimensionSerializer();
            jobj['definition']=serializer.serialize(dimensionInfo.definition);
        }
        return jobj;
    }

    deserialize(jobj:Object):DimensionInfo{
        if(!jobj){
            return null;
        }
        let dimensionInfo=new DimensionInfo();
        dimensionInfo.dataModificationAllow=jobj['dataModificationAllow'] as boolean;

        if(jobj['data']){
            dimensionInfo.data=jobj['data'].map(dimensionData=>new DimensionDataSerializer().deserialize(dimensionData));
        }

        if(jobj['definition']){
            let serializer=new DimensionSerializer();
            dimensionInfo.definition=serializer.deserialize(jobj['definition']);
        }
        return dimensionInfo;
    }
}