import { DimensionData } from '../../param-template-assignment-vo/dimension-data';
import { DataItemSerializer } from '../param-data-serializer/data-item-serializer';

export class DimensionDataSerializer{
    serialize(dimensionData:DimensionData):Object{
        if(!dimensionData){
            return null;
        }
        let jobj=new Object();
        jobj['id']=dimensionData.id;
        if(dimensionData.dataItems){
            jobj['dataItems']=dimensionData.dataItems.map(item=>new DataItemSerializer().serialize(item));
        }

        return jobj;
    }

    deserialize(jobj:Object):DimensionData{
        if(!jobj){
            return null;
        }
        let dimensionInfo=new DimensionData();
        dimensionInfo.id=jobj['id'];
        if(jobj['dataItems']){
            dimensionInfo.dataItems=jobj['dataItems'].map(item=>new DataItemSerializer().deserialize(item));
        }

        return dimensionInfo;
    }
}