import { IParameterSerializer, Parameter, ISerializeContext } from '@ecp-caf/common-structure';
import { ItemCategoryParam } from '../../extension/parameter/item-category-param';
import { BaseParameterSerializer } from './base-param-serializer';

export class ItemCategoryParamSerializer implements IParameterSerializer{
    
    serialize(param: Parameter, ctxt: ISerializeContext): Object {
        let jobj=new Object();
        if(!param){
            return jobj;
        }
        let itemCategoryParam=param as ItemCategoryParam;
        let serializer=new BaseParameterSerializer();
        jobj=serializer.serialize(itemCategoryParam,ctxt);
        jobj['kind']=itemCategoryParam.kind;
        return jobj;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext):Parameter {
        if(!obj){
            return null;
        }
        let param=new ItemCategoryParam();
        let serializer=new BaseParameterSerializer();
        param=serializer.deserialize(obj,ctxt) as ItemCategoryParam;
        return param;
    }
}