import { UbccIntegerConstraint } from '../../extension/constraint/ubcc-integer-constraint';
import { IConstraint, IConstraintSerializer, ISerializeContext } from '@ecp-caf/common-structure';

export class UbccIntegerConstraintSerializer implements IConstraintSerializer{

    serialize(constraint:IConstraint, ctxt: ISerializeContext):Object{
        let jobj=new Object();
        if(!constraint){
            return jobj;
        }
        let integerConstraint=constraint as UbccIntegerConstraint;
        jobj['kind']=integerConstraint.kind;
        jobj['max']=integerConstraint.max;
        jobj['min']=integerConstraint.min;
        jobj['unit']=integerConstraint.unit;
        return jobj;
    }

    deserialize(jobj:Object, ctxt: ISerializeContext):IConstraint{
        if(!jobj){
            return null;
        }
        let integerConstraint=new UbccIntegerConstraint();
        integerConstraint.max=jobj['max'] as number;
        integerConstraint.min=jobj['min'] as number;
        integerConstraint.unit=jobj['unit'];
        return integerConstraint;
    }

}