import { IParameterSerializer, Parameter, ISerializeContext, DefaultParameterSerializer } from '@ecp-caf/common-structure';
import { BaseParameter } from '../../extension/parameter/base-param';

export class BaseParameterSerializer implements IParameterSerializer{
    serialize(param: Parameter, ctxt: ISerializeContext): Object {
        let jobj=new Object();
        if(!param){
            return jobj;
        }
        let baseParam=param as BaseParameter;
        let serializer=new DefaultParameterSerializer();
        jobj=serializer.serialize(baseParam,ctxt);
        jobj['id']=baseParam.id;
        jobj['index']=baseParam.index;
        jobj['visible']=baseParam.visible;
        jobj['readonly']=baseParam.readonly;
        jobj['groupId']=baseParam.groupId;
        jobj['groupCode']=baseParam.groupCode;
        jobj['groupName']=baseParam.groupName;
        jobj['parameterTypeDesc']=baseParam.parameterTypeDesc;
        jobj['kind']=baseParam.kind;
        return jobj;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext):Parameter {
        if(!obj){
            return null;
        }
        let param=new BaseParameter();
        let serializer=new DefaultParameterSerializer();
        param=serializer.deserialize(obj,ctxt) as BaseParameter;
        param.id=obj['id'];
        param.index=obj['index'] as number;
        param.visible=obj['visible'] as boolean;
        param.readonly=obj['readonly'] as boolean;
        param.groupId=obj['groupId'];
        param.groupCode=obj['groupCode'];
        param.groupName=obj['groupName'];
        param.parameterTypeDesc=obj['parameterTypeDesc'];
        return param;
    }
}