import { ISerializeContext, IParameterSerializerFactory, IPropertySerializerFactory, IDataTypeSerializerFactory, IOperationSerializerFactory, DefaultOperationSerializerFactory, DefaultPropertySerializerFactory, IConstraintSerializerFactory } from '@ecp-caf/common-structure';
import { UbccParamSerializerFactory } from '../factory/ubcc-param-serializer-factory';
import { UbccDataTypeSerializerFactory } from '../factory/ubcc-data-type-serializer-factory';
import { UbccParamConstraintSerializerFactory } from '../factory/ubcc-param-constraint-serializer-factory';

export class UbccParamSerializerContext implements ISerializeContext{

    private dataTypeFactory: IDataTypeSerializerFactory;
    private parameterFactory: IParameterSerializerFactory;
    private operationFactory: IOperationSerializerFactory;
    private propertyFactory: IPropertySerializerFactory;
    private constraintFactory:IConstraintSerializerFactory;
   
    getDataTypeSerializerFactory(): IDataTypeSerializerFactory {
        if (!this.dataTypeFactory) {
            this.dataTypeFactory = new UbccDataTypeSerializerFactory();
        }
        return this.dataTypeFactory;
    }


    getParameterSerializerFactory(): IParameterSerializerFactory {
        if (!this.parameterFactory) {
            this.parameterFactory = new UbccParamSerializerFactory();
        }
        return this.parameterFactory;
    }

    getOperationSerializerFactory(): IOperationSerializerFactory {
        if (!this.operationFactory) {
            this.operationFactory = new DefaultOperationSerializerFactory();
        }
        return this.operationFactory;
    }

    getPropertySerializerFactory(): IPropertySerializerFactory {
        if (!this.propertyFactory) {
            this.propertyFactory = new DefaultPropertySerializerFactory();
        }
        return this.propertyFactory;
    }

    getConstraintSerializerFactory(): IConstraintSerializerFactory {
        if(!this.constraintFactory){
            this.constraintFactory=new UbccParamConstraintSerializerFactory();
        }
        return this.constraintFactory;
    }
  
}