import { Parameter } from '@ecp-caf/common-structure';
import { ParamExtensionConst } from '../../../const/parameter-extension-const';

export class BaseParameter extends Parameter{
    id:string;
    index:number;
    visible:boolean;
    readonly:boolean;
    groupId:string;
    groupCode:string;
    groupName:string;
    parameterTypeDesc:string;
    kind:string=ParamExtensionConst.BASE_PARAM;
    constructor(){
        super();
        this.visible=true;
        this.readonly=false;
    }
}